<template>
  <v-container>

    <v-navigation-drawer width="400px" v-model="drawer" absolute right temporary>
      <v-list-item>
        <v-list-item-content>
            <v-btn @click="drawer = !drawer" text>
              <v-icon>mdi-close</v-icon>
              
            </v-btn>

        </v-list-item-content>

      </v-list-item>

      <v-divider></v-divider>

      <v-list dense>
        <v-list-item @click="show(item)" v-for="item in metadata" :key="item.repo" link>
          <v-list-item-icon>
            <v-icon>mdi-file</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ item.repo }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-card height="100%" flat>
      <v-card-actions>

        <v-btn @click="drawer = !drawer" outlined color="primary"></v-btn>
      </v-card-actions>

      <v-card flat height="100%" v-if="selectedItem">
        <v-card-text>
          <v-card-title class="text-h4">
            {{ selectedItem.repo }}
          </v-card-title>
          <v-card-subtitle>
            <v-chip outlined color="primary" small v-for="t in selectedItem.tags" :key="t">
              {{ t }}
            </v-chip>
          </v-card-subtitle>
          <v-card-text>
            <h3>:</h3>
            <v-card outlined>
              <v-card-text>
                <v-icon>mdi-github</v-icon>
                <a :href="toGitHub(selectedItem.repo)" target="_blank">{{
                    selectedItem.repo
                }}</a>
                <br />

                <v-icon>mdi-download</v-icon>
                <a :href="selectedItem.archive.url" target="_blank">{{
                    selectedItem.archive.url
                }}</a>
              </v-card-text>
            </v-card>
            <h3>CLI:</h3>

            <v-card outlined>
              <v-card-text>
                <v-btn icon @click="copy(toCommand(selectedItem.name))">
                  <v-icon>mdi-clipboard-text-outline</v-icon>
                </v-btn>
                {{ toCommand(selectedItem.name) }}
              </v-card-text>
            </v-card>

            <h3>README:</h3>

            <v-card outlined>
              <v-card-text>
                <div v-html="readme" />
              </v-card-text>
            </v-card>
          </v-card-text>
        </v-card-text>
      </v-card>
    </v-card>
  </v-container>
</template>

<script>
import axios from "axios";
import { marked } from "marked";
export default {
  data: () => ({
    metadata: null,
    selectedItem: null,
    readme: "",
    drawer: false
  }),
  mounted() {
    axios.get("metadata/zh/gc-resource/index.json").then((res) => {
      this.metadata = res.data;
      console.log(res.data);
      this.show(res.data[0]);

    this.drawer=true;
    });

  },
  methods: {
    show(d) {
      this.selectedItem = d;

      this.drawer=false;

      this.readme = "Loading";
      axios
        .get(this.selectedItem.readme)
        .then((res) => {
          console.log(res.data);

          this.readme = marked.parse(res.data);
        });
    },

    toGitHub(g) {
      return `https://github.com/${g}`;
    },
    toCommand(n) {
      return `gc-cli install -t res -v ${n}`;
    },
    copy(t) {
      if (navigator.clipboard) {
        navigator.clipboard.writeText(t);
      }
    },
  },
};
</script>

<style>
.v-tab {
  justify-content: flex-start !important;
}
</style>