<template>
  <v-container style="height: 100%;width:100%;">

    <v-navigation-drawer width="400px" v-model="drawer" absolute right temporary>
      <v-list-item>
        <v-list-item-content>
                      <v-btn @click="drawer = !drawer" text>
              <v-icon>mdi-close</v-icon>
              
            </v-btn>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <v-list dense>
        <v-list-item @click="show(item)" v-for="item in metadata" :key="item.name" link>
          <v-list-item-icon>
            <v-icon>mdi-cube</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ item.name }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-card height="100%" flat>
      <v-card-actions>

        <v-btn @click="drawer = !drawer" outlined color="primary"></v-btn>
      </v-card-actions>

      <v-card flat height="100%" v-if="selectedItem">
        <v-card-text>
          <v-card-title class="text-h4">
            {{ selectedItem.name }}
          </v-card-title>
          <v-card-subtitle>
            <span>
              {{ selectedItem.description }}
            </span>
            <br />
            <span> :{{ selectedItem.version }} </span>
          </v-card-subtitle>
          <v-card-text>
            <h3>:</h3>
            <v-card outlined>
              <v-card-text>
                <v-icon>mdi-github</v-icon>
                <a :href="toGitHub(selectedItem.repo)" target="_blank">{{
                    selectedItem.repo
                }}</a>
                <br />

                <v-icon>mdi-download</v-icon>
                <a :href="selectedItem.file" target="_blank">{{
                    selectedItem.file
                }}</a>
              </v-card-text>
            </v-card>
            <h3>CLI:</h3>

            <v-card outlined>
              <v-card-text>
                <v-btn icon @click="copy(toCommand(selectedItem.name))">
                  <v-icon>mdi-clipboard-text-outline</v-icon>
                </v-btn>
                {{ toCommand(selectedItem.name) }}
              </v-card-text>
            </v-card>

            <h3>README:</h3>

            <v-card outlined>
              <v-card-text>
                <div v-html="readme" />
              </v-card-text>
            </v-card>
          </v-card-text>
        </v-card-text>
      </v-card>
    </v-card>
  </v-container>
</template>

<script>
import axios from "axios";
export default {
  data: () => ({
    metadata: null,
    selectedItem: null,
    drawer: false
  }),
  mounted() {
    axios.get("metadata/zh/gc-packages/index.json").then((res) => {
      this.metadata = res.data;
      console.log(res.data);
      this.show(res.data[0]);
    this.drawer=true;

    });

  },
  methods: {
    show(d) {
      this.selectedItem = d;
      this.drawer=false;

    },
    toGitHub(g) {
      return `https://github.com/${g}`;
    },
    toCommand(n) {
      return `gc-cli addpkg ${n}`;
    },
    copy(t) {
      if (navigator.clipboard) {
        navigator.clipboard.writeText(t);
      }
    },
  },
};
</script>

<style>
.card {
  height: 100% !important;
}

</style>